import Vue from 'vue'
import VueRouter from 'vue-router'
import Bikes from '../views/Bikes.vue'

Vue.use(VueRouter)

const routes = [
  {
    path: '/bikes',
    name: 'Bikes',
    component: Bikes
  }
]

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes
})

export default router
